$mainColor: #222831;
$black: #111111;
$lightBlack: #393e46;
$navbarColor: #ffb6c1;
$bgColor: #582485;
$thirdColor: #0ed1db;
$correct: limegreen;
$incorrect: red;
$white: whitesmoke;

@mixin flexCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Roboto, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  min-height: 1080px - 500px;
  background-color: $white;
  h1 {
    margin: 20px 0;
    font-size: 70px;
    @media (max-width: 500px) {
      font-size: 30px;
      text-align: center;
    }
  }
  h4 {
    margin: 20px 0;
    text-align: center;
    font-size: 50px;
    @media (max-width: 500px) {
      font-size: 40px;
    }
  }
  p {
    font-size: 30px;
    font-weight: 300;
  }
  .center {
    @include flexCenter();
    flex-direction: column;
  }
  button {
    padding: 10px 30px;
    background-color: $thirdColor;
    border: none;
    font-size: 20px;
    font-weight: 700;
    border: 1.5px solid $mainColor;
    border-radius: 5px;
    &.correct {
      background-color: $correct;
    }
    &.incorrect {
      background-color: $incorrect;
    }
  }
}

.nav-container {
  background-color: $navbarColor;
  height: 75px;
  display: flex;
  align-items: center;
  border-bottom: 2px solid $mainColor;
  nav {
    display: flex;
    justify-content: space-between;
      a {
        font-weight: 700;
        text-transform: uppercase;
        display: inline-blocks;
        height: 100%;
        text-decoration: none;
        color: $mainColor;
      }
      .navbar-brand {
        letter-spacing: 3px;
      }
  }
}

.home {
  @extend .center;
  .card {
    background-color: lightpink;
    padding: 3rem 2.5rem;
    border: 2px solid black;
    border-radius: 5px;
    margin-bottom: 50px;
    @media (max-width: 765px) {
      padding: 3rem 2rem;
    }
    @media (max-width: 500px) {
      padding: 2rem 1rem;
    }
    h3 {
      font-size: 60px;
      @media (max-width: 765px) {
        font-size: 40px;
      }
      @media (max-width: 500px) {
        font-size: 30px;
      }
    }
    h5.msg {
      text-align: center;
      @media (max-width: 500px) {
        font-size: 15px;
      }
    }
    .answers-container {
      margin-top: 40px;
    }
  }
}

.about {
  p {
    margin-bottom: 5px;
  }
  ul.tech {
    width: 100%;
    li {
      list-style: none;
      a {
        font-size: 1.5rem;
        position: relative;
      }
    }
  }
  @media (max-width: 500px) {
    p {
      font-size: 1.2em;
    }
    a {
      font-size: 1.2em;
    }
  }
}